@import '~antd/dist/antd.css';

.site-card-wrapper {
    padding: 30px;
    margin-top: 5px;
    background: #ececec;
}


@media screen and (max-width: 480px) {
    html {
        -webkit-text-size-adjust: none;
    }

    .ant-row{
        flex-direction: column;
    }

    .ant-card {
        margin-bottom: 12px;
    }

}